.footer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
  
  .main {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 24px;
    width: 100%;
    padding: 64px 0;

    .link-section {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      flex: 1;

      .menu-list {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;

        .title {
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
        }

        .link-list {
          display: flex;
          flex-wrap: wrap;
          flex-direction: row;
          width: 100%;
          gap: 24px;
          margin-top: 24px;

        }

        .toggle-list {
          margin-top: 48px;

          .toggle-item {
            margin-bottom: 16px;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;
            margin-right: 30px;
          }

         

          .theme-section {
            background-color: #b7bdc6;
            position: relative;
            width: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 24px;
            border-radius: 5px;
            transition: 0.2s all ease-in-out;

            .theme-icon {
              font-size: 16px;
              background-color: #fff;
              width: 18px;
              height: 18px;
              padding: 2px;
              border-radius: 5px;
              transition: 0.2s all ease-in-out;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              color: #000;

              path {
                color: #000;
              }
            }

            .left {
              left: 2px;
            }

            .right {
              right: 2px;
            }
          }

          .right-theme {
            background-color: #474d57 !important;
          }
        }
      }
    }

    .menu-section {
      display: flex;
      flex-direction: row;
      flex:4;
      gap: 24px;
      

      .submenu {
        display: flex;
        flex-direction: row;
        flex: 1;

        

        .menu-list {
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;

          .menu {
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            margin-top: 48px;

            @media only screen and (max-width: 450px) {

              flex-direction: column;
              justify-content: space-between;
            }
            &:first-child {
              margin-top: 0;
            }

            .title {
              font-size: 16px;
              line-height: 24px;
              margin-bottom: 12px;
            }

            .menu-item {
              margin-bottom: 6px;
              transition: 0.2s color ease-in-out;

              &:hover {
                color: var(--stand-hover-color);
              }
            }
          }
        }
      }
    }
  }



  @media only screen and (max-width: 992px) {
    .main {
      flex-direction: column;
    }

    .menu-list>.toggle-list {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: space-around;
      margin-left: 0px;
    }
  }

  @media only screen and (max-width: 768px) {
    .main>.menu-section {
      gap: 30px;
      flex-direction: column;
      width: 100%;
    }

  }

}