.bnav {
  width: 100%;

  .nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;

    .view {
      display: flex;
      flex-direction: row;

      p {
        font-size: 16px;
        cursor: pointer;
        margin-left: 20px;

        &:hover {
          color: var(--stand-color);
        }
      }
    }

    .action {
      display: flex;
      flex-direction: row;

      p {
        font-size: 16px;
        flex: 1;
        cursor: pointer;
        margin-left: 20px;

        &:hover {
          color: white
        }
      }
    }

  }

  hr {
    margin-top: 5px;
    width: 100%;
    height: 1px;
    background-color: var(--faq-no-border-color)
  }
}