.currency{ 
    display: flex;
    flex-direction: row;
    width:100%;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 0;
    padding-right: 0;
    font-size: 16px;
    cursor: pointer;
    background-color: transparent;
    .imgName{
        width:45%;
        display: flex;
        flex-direction: row;
        background-color: inherit;
        .img{
            background-color: inherit;
            width:28px;
            height:28px;
    
            img{
                width:100%;
                background-color: inherit;
            }
        }
    
        .currencyName{
            background-color: inherit;
            margin: 0;
            min-width: 0;
            margin-right: 4px;
            margin-left: 8px;
           
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            
            &:hover{
                color: #848E9C; 
            }
        }
    
    
        .currencyTotalName{
            background-color: inherit;
            color: #848E9C;
            white-space: nowrap;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
        }
    }
    

    .dollar{
        background-color: inherit;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        width: 25%;
        text-align: right;
        
        &:hover{
            color: #848E9C; 
        }
    }
    .percent{
        background-color: inherit;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        width: 25%;
        text-align: right;
        color: #0ECB81;
    }
 }